@import "../../scss/abstracts.scss";
.home {
    height: 100%;
}

.intro {
    text-align: center;
    position: relative;
    &__image {
        display: inline-block;
        cursor: pointer;
        &:hover .image {
            transform: translateX(-50%);
        }
        &:hover .greeting {
            color: #00e400 !important;
            font-weight: $font-bolder;
            z-index: 1000;
        }
    }
}

.image {
    display: inline-block;
    font-size: 10rem;
    position: relative;
    z-index: 5000;
    transition: all 0.5s ease-in-out;
}

.greeting {
    font-size: $font-small-size;
    position: absolute;
    transform: translate(-50%, -50%);
    top: 25%;
    left: 55%;
    color: transparent;
    @include respond(large-screen) {
        left: 57%;
    }
    @include respond(mid-screen) {
        left: 57%;
    }
    @include respond(smaller-screen) {
        left: 64%;
    }
}

.caption {
    color: $color-primary;
    text-align: center;
    margin: 3rem 0rem;
    position: relative;
    &__name,
    &__job {
        font-size: $font-larger-size;
        display: inline-block;
        font-weight: $font-bolder;
        margin: 0.5rem 0;
        width: 0rem;
        white-space: nowrap;
        overflow: hidden;
        @include respond(smaller-screen) {
            font-size: $font-large-size !important;
        }
        @include respond(extra-small-screen) {
            font-size: $font-larger-size !important;
        }
    }
    &__name {
        span {
            font-family: 'Jua', sans-serif;
            display: inline-block;
            @extend .gradient;
        }
        animation: typing 3s steps(25, end) forwards;
    }
    &__job {
        animation: typing 2.5s steps(25, end) both;
        animation-delay: 1.9s;
    }
}

.icons-social {
    text-align: center;
    padding-top: 1rem;
    margin-bottom: 10vh;
}

.icon {
    padding: 1rem 9rem;
    display: inline-block;
    width: 2.9rem;
    height: 2.9rem;
    background-color: $color-primary;
    cursor: pointer;
    transition: all 0.2s ease;
    @include respond(smaller-screen) {
        padding: 1rem 5rem;
        width: 3.2rem;
        height: 3.2rem;
    }
    &:hover {
        animation: gradient 0.5s ease infinite;
    }
    &--linkedin {
        -webkit-mask: url(../../assets/linkedin-in-brands.svg) no-repeat center/contain;
        mask: url(../../assets/linkedin-in-brands.svg) no-repeat center/contain;
        &:hover {
            @extend .smaller-size;
        }
    }
    &--email {
        -webkit-mask: url(../../assets/envelope-solid.svg) no-repeat center/contain;
        mask: url(../../assets/envelope-solid.svg) no-repeat center/contain;
        &:hover {
            @extend .smaller-size;
        }
    }
    &--github {
        -webkit-mask: url(../../assets/github-brands.svg) no-repeat center/contain;
        mask: url(../../assets/github-brands.svg) no-repeat center/contain;
        &:hover {
            @extend .smaller-size;
        }
    }
}

.smaller-size {
    width: 2.7rem;
    height: 2.7rem;
}

@keyframes typing {
    from {
        width: 1rem;
        visibility: hidden;
    }
    to {
        width: 94vw;
        visibility: visible;
        z-index: -100;
    }
}

@keyframes fade {
    from {
        color: $color-primary;
    }
    to {
        color: $color-tertiary;
    }
}

@keyframes appear {
    from {
        color: transparent;
    }
    to {
        color: $color-primary;
    }
}

@keyframes gradient {
    from {
        background: linear-gradient(to right, $color-secondary, #2ebf91);
    }
    to {
        background: linear-gradient(to left, $color-secondary, #2ebf91);
    }
}